/* styles/Home.module.css */

:root {
  --light-beige: #e4ceb2;
  --teal: #04c4c;
  --muted-beige: #d1b79e;
  --dark-teal: #027a6d;
  --soft-white: #ffffff;
  --warm-taupe: #b4a79e;
  --soft-charcoal: #333333;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: var(--light-beige);
}

.brand-name {
  font-size: 2rem;
  color: var(--dark-teal);
}

nav ul {
  display: flex;
  list-style: none;
}

nav ul li {
  margin-left: 20px;
}

nav ul li a {
  text-decoration: none;
  color: var(--dark-teal);
  font-weight: bold;
}

.main-content {
  padding: 3rem 1rem;
}

.hero {
  text-align: center;
  background-color: var(--teal);
  color: var(--soft-white);
  padding: 5rem 0;
}

.hero-text {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.cta-btn {
  background-color: var(--dark-teal);
  color: var(--soft-white);
  padding: 1rem 2rem;
  border: none;
  cursor: pointer;
  font-size: 1rem;
}

.cta-btn:hover {
  background-color: var(--muted-beige);
}

.products {
  margin-top: 3rem;
}

.product-list {
  display: flex;
  justify-content: space-between;
}

.product-card {
  background-color: var(--muted-beige);
  padding: 1rem;
  width: 30%;
  text-align: center;
  border-radius: 8px;
}

.footer {
  background-color: var(--light-beige);
  padding: 1rem;
  text-align: center;
}

.footer p {
  color: var(--soft-charcoal);
}
